<template>
  <div class="collection">
    <div class="rg-top" style="padding:0">
      <div class="rg-top-bottom">
        <div>
          <el-button type="primary" size="small">查看明细</el-button>
          <el-button type="primary" size="small">导出</el-button>
        </div>
      </div>
    </div>
    <div class="rg-top" style="margin:10px">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="税款所属期">
          <el-date-picker type="date" placeholder="选择日期" style="width: 48%;" value-format="yyyy-MM-dd"></el-date-picker>
          -
          <el-date-picker type="date" placeholder="选择日期" style="width: 48%;" value-format="yyyy-MM-dd"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small">查询</el-button>
          <el-button type="primary" size="small">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div style="margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="dataHistory" border style="max-width:100%">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="所得月份">
            <template>
              {{ company[0].shenbao_month }}
            </template>
          </el-table-column>
          <el-table-column prop="shenbaobiao" label="申报表"></el-table-column>
          <el-table-column prop="tuibu" label="应补（退）税额"></el-table-column>
          <el-table-column prop="shijiao" label="实缴税额"></el-table-column>
          <el-table-column prop="status" label="缴款状态"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  components: {},
  data () {
    return {}
  },
  computed: {
    ...mapGetters(['company']),
    dataHistory () {
      return this.$store.state.form.jiaokuanForm
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },
  mounted () {

  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 10px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 10px 10px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }

  .rg-top .el-form-item {
    margin: 10px 0;
  }

  .rg-top-bottom[data-v-10f73d38] {
    line-height: 35px
  }
</style>
<style>
  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .person-status.el-form-item {
    margin-bottom: 5px;
  }

  [data-v-130d4e25] .el-radio-group {
    float: right;
    position: relative;
    right: -140px;
  }

  .el-form-item {
    margin-bottom: 18px;
  }

  [data-v-130d4e25] .el-radio__input {
    display: none;
  }

  [data-v-130d4e25] .el-radio.el-radio--medium {
    margin-right: 10px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }
</style>



